/*
Структура разметки:
<div class="imagepicker__container">
    <label class="imagepicker">
        <img class="imagepicker__miniature img-responsive" src="{{ $logo }}">
        <input class="imagepicker__input" name="logo" id="logo" type="file" data-toggle="imagepicker" data-src="{{ isset($logo) ? $logo : '' }}">
    </label>
</div>
*/
.imagepicker__container {
  margin-left: -15px;
  margin-right: -15px;
}
.imagepicker__container .imagepicker {
  position: relative;
  min-height: 1px;
  padding-left: 15px;
  padding-right: 15px;
  overflow: hidden;
}
@media (min-width: 1200px) {
  .imagepicker__container .imagepicker {
    float: left;
    width: 33.33333333%;
  }
}
.imagepicker__container .imagepicker__hover-place {
  position: relative;
  display: block;
  width: 300px;
  height: 300px;
  background: url("/img/imgpic_icon_photo.png") no-repeat center;
  background-size: 64px, 64px;
  cursor: pointer;
  padding: 5px;
  border: 1px solid #7a7a7a;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}
.imagepicker__container .imagepicker__hover-place:after {
  content: " ";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #000000 url("/img/imgpic_icon_add.png") no-repeat center;
  background-size: 64px, 64px;
  opacity: 0;
  border-top-right-radius: 2px;
  border-top-left-radius: 2px;
  border-bottom-right-radius: 2px;
  border-bottom-left-radius: 2px;
  -webkit-transition: opacity 0.6s ease-out 0.1s;
  -o-transition: opacity 0.6s ease-out 0.1s;
  transition: opacity 0.6s ease-out 0.1s;
}
.imagepicker__container .imagepicker__hover-place:hover:after {
  opacity: 0.7;
}
.imagepicker__container .imagepicker:hover .imagepicker__action {
  right: 0;
}
.imagepicker__container .imagepicker__miniature {
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}
.imagepicker__container .imagepicker__input {
  display: none;
}
.imagepicker__container .imagepicker .imagepicker__action {
  position: absolute;
  right: -100px;
  top: 0;
  width: 100px;
  height: 100%;
  padding: 5px;
  background: rgba(255, 255, 255, 0.5);
  border-left: 1px solid #adadad;
  -webkit-transition: right 0.6s ease-out 0.1s;
  -o-transition: right 0.6s ease-out 0.1s;
  transition: right 0.6s ease-out 0.1s;
}
.imagepicker__container .imagepicker .imagepicker__btn {
  display: block;
  margin: 0 auto 15px auto;
  padding: 5px;
  width: 34px;
  height: 34px;
  border: 1px solid #adadad;
  outline: none;
  color: #ecf0f1;
  border-radius: 4px;
  -webkit-transition: background 0.6s ease-out 0.1s;
  -o-transition: background 0.6s ease-out 0.1s;
  transition: background 0.6s ease-out 0.1s;
}
.imagepicker__container .imagepicker .imagepicker__btn.imagepicker__btn_remove {
  background: #e74c3c;
}
.imagepicker__container .imagepicker .imagepicker__btn.imagepicker__btn_remove:hover {
  background: #c0392b;
}
